* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

h2, p {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-image: none;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  transition: all .3s;
}

.nav__list li:first-child .nav__link {
  margin-left: 0;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card-img {
  justify-content: center;
  align-items: center;
  padding: 32px 19px 40px;
  display: flex;
}

.card-img__product {
  width: 160px;
  height: 134px;
  display: block;
}

.card-reviews {
  color: #060b35;
  padding-inline: 16px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card-reviews {
  color: #34568b;
  transition: all .3s;
}

.card-code {
  color: #616070;
  margin-top: 4px;
  margin-left: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card-count {
  justify-content: space-between;
  margin-top: 24px;
  padding-inline: 16px;
  display: flex;
}

.card-count__price {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card-count__value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card-button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card-button:hover {
  color: #00acdc;
  background-color: #fff;
  transition: all .3s;
}

.quality {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  padding-inline: 16px;
  display: flex;
}

.stars {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 li:not(li:last-child) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star:not(li:last-child) {
  margin-right: 4px;
}

.reviews {
  color: #060b35;
  padding-top: 2px;
  font-size: 10px;
  line-height: 14px;
}

